.header {
  background: #f1f1f1;
  border-bottom: 1px solid #e5e5e5;
}
.header-inner {
  width: 960px;
  margin: 0 auto;
  height: 72px;
  background: url("../assets/logo.png") left center no-repeat;
}
.nav {
  float: right;
}
.nav a {
  display: inline-block;
  line-height: 72px;
  margin-left: 40px;
  text-decoration: none;
  font-size: 18px;
  color: #666;
}
.nav a:hover {
  color: #f04c71;
}
