
*
  margin: 0
  padding: 0
body
  font-family: "Microsoft Yahei", Helvetica, Arial, FreeSans, Arimo, "Droid Sans", "wenquanyi micro hei", "Hiragino Sans GB", "Hiragino Sans GB W3", sans-serif
.main
  width: 960px
  margin: 30px auto 0
  overflow: auto
.lot
  margin-top: 80px
  width: 630px
  height: 485px
  background: url("../assets/machine.png") center no-repeat
  background-size: 100% auto
  position: relative
  float: left
  h1
    text-align: center
    text-indent: -60px
    font-size: 48px
    margin-top: -74px
    color: #333

.lot-wrap
  position: absolute
  left: 66px
  top: 164px
  width: 420px
  height: 175px
  overflow: hidden
  background: #f04c71

.lot-start
  width: 68px
  height: 240px
  position: absolute
  right: 0

.lot-box
  width: 210px
  position: absolute
  top: 0
  &:nth-child(1)
    left: 4px
  &:nth-child(2)
    left: 208px
  &.gift .lot-item .lot-item-text
    &.s
      font-size: 30px
    &.m
      font-size: 34px
    &.l
      font-size: 44px
  .lot-item
    width: 210px
    text-align: center
    height: 166px
    line-height: 35px
    display: table
    .lot-item-text
      background: #fff
      text-align: center
      font-size: 48px
      display: table-cell
      vertical-align: middle
      line-height: 1.3
      border: 10px solid #f04c71
      word-break: break-all

.res
  width: 240px
  height: 550px
  padding: 12px 10px
  float: right
  border: 12px solid #f04c71
  border-radius: 20px
  overflow: scroll
  margin-left: 40px
  font-size: 16px
  .res-item
    line-height: 20px
    margin: 10px 0

.btn-group
  button
    width: 100px
    height: 50px
    font-size: 24px



